@import "../../routes/styles";

.topic_main {
  @include for-phone-only{
    padding: 25px 0;
  }
  padding: 50px 0;
  min-height: calc(50vh);

  .topic_main_header {
    @include for-phone-only{
      font-size: 40px;
      padding: 0 10px 20px;
    }
    text-align: center;
    font-family: 'Permanent Marker', cursive;
    font-size: 56px;
    text-shadow: 1px 0 1px #CCCCCC, 0 1px 1px #EEEEEE, 2px 1px 1px #CCCCCC, 1px 2px 1px #EEEEEE, 3px 2px 1px #CCCCCC, 2px 3px 1px #EEEEEE, 4px 3px 1px #CCCCCC, 3px 4px 1px #EEEEEE, 5px 4px 1px #CCCCCC, 4px 5px 1px #EEEEEE, 6px 5px 1px #CCCCCC, 5px 6px 1px #EEEEEE, 7px 6px 1px #CCCCCC, 2px 2px 2px rgba(206, 89, 55, 0);
    padding-bottom: 20px;
  }

  .container {
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: stretch;
  }
}